



















import utils from '../../utils/utils.js'
import globalData from '../../utils/globalData.js'
import {
	formid,
	getUserOpenId,
	bindGetUserInfo
} from "../../utils/common.js";
export default {
	data() {
		return {
			page_number: 1,
			page_size: 10,
			list: [],
			arrList: [],
			count: 0,
			htmlShow: true,
			htcres: null,
			complete:false
		};
	},
	onShow() {
		this.list=[];
		this.arrList=[];
		this.page_number=1;
		this.load()
	},
	onLoad(e) {
		uni.showLoading({
					title: '加载中'
				});
	},
	computed: {

	},
	methods: {
		load() {
			var that = this;
			getUserOpenId().then((key) => {
				var datas = {
					sessionKey: key,
					page_number: this.page_number,
					page_size: this.page_size
				}
				utils.requst("user/invite", "POST", datas).then((res) => {
					uni.hideLoading();
					if (res.data.list) {
						for (let i = 0; i < res.data.list.length; i++) {
							that.arrList.push(res.data.list[i])
						}
						that.list = that.arrList;
						that.count = res.data.count;
						that.htmlShow = true;
						that.htcres = res.data;
						 if (that.arrList.length == that.count) {
								that.complete = true
						}
					}
				})
			})
		}
	},
	onReachBottom() {
		var that = this;
		var count = that.count,
			page_size = that.page_size,
			page_number = that.page_number;
		var num = Math.ceil(count / page_size) < 11 ? Math.ceil(count / page_size) : 10;
		if (page_number < num) {
			that.page_number = 1 + parseInt(page_number)
			that.load();
		} else {
			that.complete = true
		}
	},
};
